<template>
  <div class="media">
    <!-- <div class="header-btn-area">
      <el-button type="primary" icon="el-icon-plus" @click="showAdd"></el-button>
    </div> -->
    <el-table :data="mediaReportList">
      <el-table-column label="Logo">
        <template slot-scope="scope">
          <img class="table-image" :src="scope.row.logoUrl" alt="">
        </template>
      </el-table-column>
      <el-table-column label="ID">
        <template slot-scope="scope">{{ scope.row.projId }}</template>
      </el-table-column>
      <el-table-column label="ID">
        <template slot-scope="scope">{{ scope.row.socialId }}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{ scope.row.title }}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{ scope.row.officialName }}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{ scope.row.postTime }}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <!-- <el-button size="mini" @click="showEdit(scope.$index)"></el-button> -->
          <el-button size="mini" type="danger" @click="showDel(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data () {
    return {
      mediaReportList: []
    }
  },
  mounted () {
    this.updateData()
  },
  methods: {
    updateData () {
      this.$http.get('/api/getMediaReportList').then((res) => {
        if (res.data.errcode === 0) {
          this.mediaReportList = res.data.data.medisReportList
        }
      })
    },
    showDel (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.delMediaReport(id)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    delMediaReport (id) {
      this.$http.post('/api/delMediaReport', {
        id: id
      }).then((res) => {
        if (res.data.errcode === 0) {
          this.$message({ type: 'success', message: '!' })
          this.updateData()
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
</style>



